import Vue from 'vue';
import Router from 'vue-router';
// import Store from './store.js';
import Home from './views/Home.vue';
import Login from './views/Login';
import Readme from './views/Readme.vue';

Vue.use(Router);
const routes = [{
  path: '/',
  name: 'home',
  component: Home,
  redirect: '/H5Video.html',
    children: [{
      path: 'H5Video.html',
      name: 'H5Video.html',
      component: Login
    },
    {
      path: 'readme',
      name: 'readme',
      component: Readme
    }]
}];
const router = new Router({
  mode: 'hash',
  routes
});

