import Vue from 'vue';
import Vuex from 'vuex';
import platform from 'platform';

Vue.use(Vuex);

const publicConfig = window.publicConfig;
export default new Vuex.Store({
  state: {
    client: {
      QueuePlay: false,
      businessIndex: 0, // 业务的index
      connectionState: 'unconnected',
      accountName: '',
      peerName: '',
      peerUri: '',
      config: {
        clarity: undefined
      },
      selectedDevice: {
        audioInput: undefined,
        audioOutput: undefined,
        videoInput: undefined,
        videoOutput: undefined
      }
    },
    platform: {
      browser: {
        name: platform.name, // 浏览器名称
        version: platform.version, // 浏览器版本
        appName: (platform.ua.includes('MicroMessenger') || platform.ua.includes('TBS')) ? 'WeiChat' : undefined // 判断是否为app的内置浏览器
      },
      system: {
        name: platform.os.family, // 系统名称
        version: platform.os.version, // 系统版本
        architecture: platform.os.architecture // 系统架构
      },
      device: {
        type: /Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent) ? 'Mobile' : 'PC'
      }
    },
    // debug: publicConfig.debug
  },
  mutations: {
    setAccountName(state, data) {
      state.client.accountName = data;
    },
    setSelectedDevice(state, data) {
      state.client.selectedDevice = data;
    },
    setClarity(state, data) {
      state.client.config.clarity = data;
    },
    setBussinessIndex(state, data) {
      state.client.businessIndex = data;
    },
    setQueuePlay(state, data) {
      state.client.QueuePlay = data;
    },
    changeConnectionStatus(state, data) {
      state.client.connectionState = data;
    },
    setPeerName(state, data) {
      state.client.peerName = data.nick;
      state.client.peerUri = data.uri;
    }
  },
  actions: {
  }
});
