<template>
  <div class="login">
    <el-card v-if="!visibleBusinessConfig" class="mainCard" shadow="always">
      <div class="login-card-body">
        <div class="login-title">
          <p>远程银行客户服务</p>
        </div>
        <div class="box box1">
          <img src="../assets/images/gdyh.jpg" alt="logo" class="img">
          <div class="call-botton" @click="login">
            <template v-if="logining">
              <span class="loading"><i class="el-icon-loading loading-icon"></i></span>
            </template>
          </div>
          <div class="readme">
            <el-checkbox v-model="checked"></el-checkbox>
            <router-link to="readme">已阅读《视频服务须知》</router-link>
          </div>
        </div>
        <div class="box box2">
          <a href="https://h5.eqxiu.com/s/eWwpkv5f">
            <img src="../assets/images/box2.png" alt="logo" class="img">
          </a>
        </div>
        <div class="box box3">
          <el-carousel :interval="3000" arrow="never" class="carousel-box">
            <el-carousel-item>
              <a href="https://mp.weixin.qq.com/s/C_bQXjL6Yeet7Kr9qye-xg">
                <img src="../assets/images/lb1.png">
              </a>
            </el-carousel-item>
            <el-carousel-item>
              <a href="https://mp.weixin.qq.com/s/8tNy42ln6KFOoLxrJDuWyQ">
                <img src="../assets/images/lb2.png">
              </a>
            </el-carousel-item>
            <el-carousel-item>
              <a href="https://mp.weixin.qq.com/s/K0tgRSosE2-Uf4uQ98qRtA">
                <img src="../assets/images/lb3.png">
              </a>
            </el-carousel-item>
            <el-carousel-item>
              <a href="https://mp.weixin.qq.com/s/miUmdW4YF4HP2AO7sGvKyw">
                <img src="../assets/images/lb4.png">
              </a>
            </el-carousel-item>
            <el-carousel-item>
              <a href="https://mp.weixin.qq.com/s/qdCxWhTP6_mFfqsEAAX3xA">
                <img src="../assets/images/lb5.png">
              </a>
            </el-carousel-item>
            <el-carousel-item>
              <a href="https://mp.weixin.qq.com/s/ODnfPEwakty-2E_Ww9mGHQ">
                <img src="../assets/images/lb6.png">
              </a>
            </el-carousel-item>
          </el-carousel>
        </div>
       </div>
    </el-card>
    <el-dialog
      class="code-dialog"
      title="请滑动验证"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      width="290px"
      :before-close="handleClose"
      >
      <!-- :visible.sync="dialogVisible" -->
      <!-- :before-close="handleClose" -->
    <div>
        <div  class="verify">
          <div v-for="item in jsons" :key="item.data.YzmToken">
            <img :src="item.data.bigImgData" alt="图片验证"  class="large_icon">
            <img :src="item.data.smallImgData" alt="拼图"  class="jigsaw">
          </div>
          <div ref="verifys" class="verifys">
            <div ref="small_icon" class="small_icon">&gt;</div>
          </div>
        </div>
    </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { fetchPost } from '../../src/api/helpers';
export default {
  name: 'H5video',
  data() {
    return {
      jsons:[],
      lock:true,
      dialogVisible: false,
      logining: false,
      visibleBusinessConfig: false, // 是否现实商务设置
      checked: false,
      Updatas: false
    };
  },
  created() {
    // 滑动解锁的初始数据
    this.$axios.get(this.$loca).then(res=>{
      res.data.bigImgData = "data:image/png;base64," + res.data.bigImgData;
      res.data.smallImgData ="data:image/png;base64," + res.data.smallImgData;
      this.jsons.push(res)
   
    },err =>{
        this.$message({
          duration: 2000,
          message: '请检查网络,稍后再试',
          type: 'warning'
        });
    })
  },
  computed: {
  },
  mounted() {
  },
  beforeDestroy() {
  },
  methods: {
    handleClose() {
      this.dialogVisible = false;
      this.logining = false;
    },
    login() {
      if ((new Date().getHours() < 9) || (new Date().getHours() > 18)) {
        this.$confirm('尊敬的客户：视频服务时间为9:00-19:00，建议您在工作时间拨打，感谢您的理解！', '提示', {
          closeOnHashChange: false,
          showClose: false,
          closeOnClickModal: false,
          showCancelButton: false,
          confirmButtonText: '确定'
        });
        return;
      }
      if (this.logining) {
        return;
      }
      if (!this.checked) {
        this.$message.warning('请阅读并勾选《视频服务须知》，感谢您的配合');
        return;
      }
      if (this.jsons.length == 0) {
        return;
      }
      this.dialogVisible = true;
      this.logining = true;
    
      // 滑动逻辑实现
      var self=this;
      self.$nextTick(()=>{
          // 定义图片的逻辑
          //一、定义了一个获取元素的方法
          function getEle(selector){
              return document.querySelector(selector);
          }
          //二、获取到需要用到的DOM元素
          var box = getEle(".verifys"),//底部容器
              slider = getEle(".small_icon"),//底部滑块
              jigsaw = getEle('.jigsaw'),
              maxMoveX = box.offsetWidth- 50,//解锁可以滑动的距离 40是写死的
              downX,//用于存放鼠标按下时的位置
              ptoken,//验证成功后的token携带给排队页面
              retCode='fail';//是否滑动成功
              jigsaw.style.top = self.jsons[0].data.location_y + 'px';
          if (self.Updatas) {
              self.$axios.get(self.$loca).then(res=>{
                self.jsons = [];
                res.data.bigImgData = "data:image/png;base64," + res.data.bigImgData;
                res.data.smallImgData ="data:image/png;base64," + res.data.smallImgData;
                self.jsons.push(res)
                jigsaw.style.top = self.jsons[0].data.location_y + 'px';
                self.lock = true;
              },err =>{
                  self.$message({
                    duration: 2000,
                    message: '网络繁忙,请稍后再试',
                    type: 'warning'
                  });
              })
          }
          //三、给滑块添加鼠标按下事件
          slider.onmousedown = mousedownHandler;
          slider.ontouchstart = mousedownHandler;//移动端加touchstart事件
          //3.1鼠标按下事件的方法实现
          function mousedownHandler(e){
            if (!self.lock) {
              return
            }
              slider.style.transition = "";
              jigsaw.style.transition = "";
              var e = e || window.event || e.which;
              downX = e.clientX ? e.clientX : e.changedTouches[0].clientX;
              if(retCode == 'fail' ){
                  //在鼠标按下时，分别给鼠标添加移动和松开事件
                  document.onmousemove = mousemoveHandler;
                  document.onmouseup = mouseupHandler;
                  //添加移动端对应事件
                  document.ontouchmove = mousemoveHandler;
                  document.ontouchend = mouseupHandler;
              }
              
          };
          //四、定义一个获取鼠标当前需要移动多少距离的方法
          function getOffsetX(offset,min,max){
              if(offset < min){
                  offset = min;
              }else if(offset > max){
                  offset = max;
              }
              return offset;
          }
          //3.1.1鼠标移动事件的方法实现
          function mousemoveHandler(e){

              var e = e || window.event || e.which;
              var moveX = e.clientX?e.clientX:e.changedTouches[0].clientX;
              var offsetX = getOffsetX(moveX - downX,0,maxMoveX);
              slider.style.left = offsetX + "px";
              jigsaw.style.left = offsetX + "px";
          
              // e.preventDefault();
          };
          //3.1.2鼠标松开事件的方法实现
          function mouseupHandler(e){
                self.lock = false;
                document.onmousemove = null;
                document.onmouseup = null;
                //移除移动端事件
                document.ontouchmove = null;
                document.ontouchend = null;
                //滑动成功时，移除鼠标按下事件和鼠标移动事件
                slider.onmousedown = null;
                document.onmousemove = null;
                //移除移动端事件
                document.ontouchstart = null;
                document.ontouchmove = null;


              var moveX = e.clientX?e.clientX:e.changedTouches[0].clientX;
              var endX = getOffsetX(moveX - downX,0,maxMoveX);
              endX = endX.toFixed();

              // 将endX的值传递给后端判断
              self.$axios.post(self.$tokenX, {"location_y":self.jsons[0].data.location_y,"location_x":endX,"yzmToken":self.jsons[0].data.yzmToken}).then(res=>{
                console.log(res)
               retCode = res.data.retCode;
               console.log(retCode)
                if(retCode == 'success'){
                  self.Updatas = true;
                  // self.jsons = [];
                  //五、定义一个滑块解锁成功的方法
                    slider.style.left = 0 + "px";
                    jigsaw.style.left = 0 + "px";
                    //滑动成功时，移除鼠标按下事件和鼠标移动事件
                    slider.onmousedown = null;
                    document.onmousemove = null;
                    //移除移动端事件
                    document.ontouchstart = null;
                    document.ontouchmove = null;
                    // 成功了跳转页面
                    self.handleClose()
                    console.log(`${self.$lineUp}${res.data.videoToken}`)
                    window.location.href = `${self.$lineUp}${res.data.videoToken}`
                } else {
                   // 给出错误提示
                    self.$message({
                      duration: 1500,
                      message: '认证失败！请重新认证',
                      type: 'warning'
                    });
                    slider.style.left = 0 + "px";
                    jigsaw.style.left = 0 + "px";
                    slider.style.transition = "left 0.5s linear";
                    jigsaw.style.transition = "left 0.5s linear";
                    // 重新请求
                    self.$axios.get(self.$loca).then(res=>{
                      self.jsons = [];
                      console.log('12313132',res)
                      res.data.bigImgData = "data:image/png;base64," + res.data.bigImgData;
                      res.data.smallImgData ="data:image/png;base64," + res.data.smallImgData;
                      self.jsons.push(res)
                      jigsaw.style.top = self.jsons[0].data.location_y + 'px';
                      self.lock = true;
                    },err =>{
                        self.$message({
                          duration: 2000,
                          message: '网络繁忙,请稍后再试',
                          type: 'warning'
                        });
                    })

                }
             
              },err =>{
                  self.$message({
                    duration: 2000,
                    message: '网络繁忙,请稍后再试',
                    type: 'warning'
                  });
              });
          };
         
      });
    }
}
}
</script>


